
import { defineComponent } from 'vue'

interface ChartPageHeaderData {
  currentGraph: string;
}

export default defineComponent({
  name: 'ChartPageHeader',
  watch: {
    currentGraph: function () {
      this.$emit('changeGraph', this.currentGraph)
    }
  },
  data (): ChartPageHeaderData {
    return {
      currentGraph: 'pie'
    }
  }
})
